	module.exports = {"data":{"markdownRemark":{"html":"<h3>Day 1</h3>\n<h4>1. Thinking Like a Programmer</h4>\n<ul>\n<li>Basic Problem Solving</li>\n<li>Puzzle Solving, looking for possible solutions</li>\n<li>Breaking down a Problem</li>\n<li>Listing Instructions</li>\n<li>Deconstructing word problems</li>\n</ul>\n<h4>2. HTML Part 1</h4>\n<ul>\n<li>HTML 5</li>\n<li>Tags &#x26; Elements</li>\n<li>Document Structure</li>\n<li>Tags and Their Attributes</li>\n</ul>\n<hr>\n<h3>Day 2</h3>\n<h4>3. HTML Part 2</h4>\n<ul>\n<li>Semantic Tags (Header, Body, Footer)</li>\n<li>Outlining a Doc with Tags</li>\n<li>Nav, Table Elements</li>\n<li>Forms and Input Elements</li>\n<li>Submitting The Form</li>\n</ul>\n<h4>4. CSS Part 1</h4>\n<ul>\n<li>What is CSS</li>\n<li>CSS Syntax</li>\n<li>Basic Properties and Values</li>\n<li>Selectors</li>\n<li>Pseudo-classes</li>\n<li>\n<h2>CSS File w/ <link> tag</h2>\n</li>\n</ul>\n<h3>Day 3</h3>\n<h4>5. CSS Part 2</h4>\n<ul>\n<li>Expanding on Properties</li>\n<li>Styling Text</li>\n<li>CSS Border</li>\n<li>Displaying Content</li>\n<li>CSS Box Model</li>\n<li>Margin v Padding</li>\n<li>Placing Content / Layout</li>\n</ul>\n<h4>6. JavaScript Part 1</h4>\n<ul>\n<li>JavaScript’s role in Webpages</li>\n<li>JavaScript vs Java</li>\n<li>Script Tags &#x26; Linking *.js files</li>\n<li>Types</li>\n<li>Variables</li>\n<li>Conditional if else statements</li>\n<li>Comparison Operators</li>\n<li>\n<h2><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Comparison_Operators\">https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Comparison_Operators</a>)</h2>\n</li>\n</ul>\n<h3>Day 4</h3>\n<h4>7. JavaScript Part 2</h4>\n<ul>\n<li>Arrays</li>\n<li>Array Properties and Methods</li>\n<li>Loops!</li>\n<li>For Loops.</li>\n<li>Looping Through Array</li>\n</ul>\n<h4>8. JavaScript Part 3</h4>\n<ul>\n<li>Functions. Structor and Syntax</li>\n<li>Calling or Invoking a Function</li>\n<li>Scope, Global / Local</li>\n<li>Document Object Model (DOM)</li>\n<li>Retrieving DOM Elements</li>\n<li>Manipulating DOM Elments</li>\n<li>HTML Event Handling</li>\n</ul>\n<hr>\n<h3>Day 5</h3>\n<h4>9. The Terminal &#x26; Source Control</h4>\n<ul>\n<li>Command Line Interface (CLI)</li>\n<li>Commands parameters / Paths</li>\n<li>Showing files and changing directories</li>\n<li>Source Control Git</li>\n<li>Commits, Repos, Github, Branches</li>\n<li>Local v Remote Repos</li>\n</ul>\n<h4>10. Final Project CFS</h4>\n<ul>\n<li>Coding From Scratch, you will be building a form, use the data inputted in the form to create a basic ID card.</li>\n</ul>\n<hr>","headings":[{"depth":3,"value":"Day 1"},{"depth":4,"value":"1. Thinking Like a Programmer"},{"depth":4,"value":"2. HTML Part 1"},{"depth":3,"value":"Day 2"},{"depth":4,"value":"3. HTML Part 2"},{"depth":4,"value":"4. CSS Part 1"},{"depth":2,"value":"CSS File w/ "},{"depth":3,"value":"Day 3"},{"depth":4,"value":"5. CSS Part 2"},{"depth":4,"value":"6. JavaScript Part 1"},{"depth":2,"value":"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Comparison_Operators"},{"depth":3,"value":"Day 4"},{"depth":4,"value":"7. JavaScript Part 2"},{"depth":4,"value":"8. JavaScript Part 3"},{"depth":3,"value":"Day 5"},{"depth":4,"value":"9. The Terminal & Source Control"},{"depth":4,"value":"10. Final Project CFS"}],"frontmatter":{"title":"Week 1 - Coding From Scratch"}}},"pathContext":{"slug":"/courses/coding-from-scratch/syllabus/"}}

